
.about {
    background-color: rgb(240, 243, 245);
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    line-height: 0.5;
}

.sub-heading {
    text-align: center;
    color: #27ae60;
    font-size: 1.5rem;
    padding-top: 1rem;
}

.heading {
    text-align: center;
    color: #192a56;
    font-weight: 1000;
    font-size: 2.5rem;
    padding-bottom: 2rem;
    text-transform: uppercase;
}

.row {
    display: inline-flex;
}

.image1 {
    width: 50%;
}
.image2{
    width: 50%;
    padding-right: 5%;
}
.image2 h3{
    font-size: 2.5rem;
    color: #192a56;
}
.image2 p{
    text-align: justify;
    font-size: 1.1rem;
    color: rgb(79, 79, 79);
    line-height: 2 !important;
}
.logo1 {
    width: 70%;
    margin-left: 15%;
    border-radius: 5rem;
}