.card{
    width:25%;
    display: inline;
    height: max-content;
    margin-bottom: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    padding-left: 3rem;
    padding-right: 3rem;
}
.card:hover{
    animation: shrink .3s;
    background-color: #aeadad;
    color: #192a56;
    text-decoration: none;
}
@keyframes shrink {
    0%{
        rotate: 3deg;
    }
    25%{
        rotate:-3deg;
    }
    50%{
        rotate:3deg;
    }
    95%{
        rotate:-3deg;
    }
    100%{
        rotate: 0deg    ;
    }
}
