.container20 {
    position: relative;
    width: 40rem;
    max-width: 100%;
    height: 43rem;

}

.image20 {
    position: absolute;
    /* border-radius: 50%; */
    text-align: center;
    margin-left: 5rem;
    width: 100%;
    opacity: 0;
    animation: change-img-anim 9s infinite;
}

.image20:nth-of-type(1) {
    animation-delay: 0s;
}

.image20:nth-of-type(2) {
    animation-delay: 4s;
}

.image20:nth-of-type(3) {
    animation-delay: 8s;
}

@keyframes change-img-anim {
    0% {
        opacity: 0;
    }

    10% {
        opacity: 1;
    }

    90% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

   /* name animation*/

 .text1 {
    position: relative; 
    margin-top: -8%;
}

 .text1::before {
    content: "";
    color: #ff7f50;
    animation: words 20s infinite;
    font-size: 4.5rem;
    font-weight: bolder;
}

 .text1::after {
    content: "";
    position: absolute;
    width: calc(100% + 8px);
    height: 100%;
    background-color: #ffffff;
    border-left: 2px solid #ff7f50;
    right: 1px;
    animation: cursor .8s infinite, typing 10s steps(10) infinite;
}

@keyframes cursor {
    to {
        border-left: 2px solid #ff7f5000;
    }
}

@keyframes words {

    0%,
    33% {
        content: "Bathroom Shelf";
    }

    33%,
    66% {
        content: "Soap Rack SS";
    }

    66%,
    100% {
        content: "Towel Rack";
    }
}

@keyframes typing {

    10%,
    15%,
    30%,
    35%,
    50%,
    55%,
    70%,
    75%,
    90%,
    95% {
        width: 0;
    }

    5%,
    20%,
    25%,
    40%,
    45%,
    60%,
    65%,
    80%,
    85% {
        width: calc(100% + 8px);
    }
}




























